html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--off-white);
}
body.noscroll {
  overflow: hidden;
}

img {
  margin: 0;
  vertical-align: middle;
}

picture {
  margin: 0;
}
picture img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

p {
  margin: 0;
  padding: 0;
}

a,
a:visited,
a:hover {
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

input:focus {
  outline: none;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.flex {
  display: flex;
}
.flex-jc-sb {
  justify-content: space-between;
}
.flex-jc-c {
  justify-content: center;
}
.flex-ai-c {
  align-items: center;
}
.flex-ai-b {
  align-items: baseline;
}
.flex-d-c {
  flex-direction: column;
}
.flex-d-r {
  flex-direction: row;
}

@media (max-width: 63.9375em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 64em) {
  .hide-for-desktop {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.img-responsive {
  max-width: 100%;
  height: auto;
}

button {
  padding: 0;
  margin: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.attribution {
  font-size: 15px;
  text-align: center;
  margin-bottom: 15px;
  color: var(--dark-grayish-blue);
}

.attribution a {
  color: hsl(228deg, 45%, 44%);
  font-weight: 700;
}

:root {
  --soft-orange: hsl(35, 77%, 62%);
  --soft-red: hsl(5, 85%, 63%);
  --off-white: hsl(36, 100%, 99%);
  --grayish-blue: hsl(233, 8%, 79%);
  --dark-grayish-blue: hsl(236, 13%, 42%);
  --very-dark-blue: hsl(240, 100%, 5%);
}

@font-face {
  font-family: InterX;
  src: url("/assets/fonts/static/Inter-Bold.ttf") format("ttf"), url("/assets/fonts/static/Inter-ExtraBold.ttf") format("ttf"), url("/assets/fonts/static/Inter-Regular.ttf") format("ttf");
}
:root {
  --fs-base: 1rem;
  --fs-scale-ratio: .8;
  --fs-xxlg: calc(var(--fs-base) * var(--fs-scale-ratio) * 3.375);
  --fs-xlg: calc(var(--fs-base) * var(--fs-scale-ratio) * 2.25);
  --fs-lg: calc(var(--fs-base) * var(--fs-scale-ratio) * 2.25);
  --fs-md: calc(var(--fs-base) * var(--fs-scale-ratio) * 1.1875);
  --fs-sm: calc(var(--fs-base) * var(--fs-scale-ratio) * 1);
  --fs-xs: calc(var(--fs-base) * var(--fs-scale-ratio) * 0.9375);
}

@media (min-width: 36em) {
  :root {
    --fs-scale-ratio: .85;
  }
}
@media (min-width: 48em) {
  :root {
    --fs-scale-ratio: .9;
  }
}
@media (min-width: 64em) {
  :root {
    --fs-scale-ratio: 1;
  }
}
@media (min-width: 87.5em) {
  :root {
    --fs-scale-ratio: 1.1;
  }
}
body {
  font-family: "Inter", sans-serif;
  font-weight: 400;
  font-size: 0.9375rem;
  color: var(--grayish-blue);
}

p {
  line-height: 1.8;
}

h1 {
  font-size: var(--fs-xxlg);
  font-weight: 900;
  line-height: 1.3;
}

h2 {
  font-size: var(--fs-xlg);
  font-weight: 700;
}

h3 {
  font-size: 1rem;
}

h4 {
  font-size: 0.9375rem;
}

.container {
  max-width: 90%;
  margin: 0 auto;
}
@media (min-width: 87.5em) {
  .container {
    max-width: 85rem;
  }
}